.outer {
  padding-left: 24px;
  padding-right: 24px; }

.inner {
  margin-left: auto;
  margin-right: auto;
  max-width: 1136px;
  width: 100%; }

.section-padding {
  padding-top: 3rem;
  padding-bottom: 3rem; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1rem;
  margin-right: -1rem; }

.col {
  flex: 1 1 300px;
  margin-left: 1rem;
  margin-right: 1rem; }
.grid {
  &__item.grid--7 {
    lost-column: 7/12 flex; }

  &__item.grid--5 {
    lost-column: 5/12 flex; } }

a {
  color: darkblue; }
